<script>
  import {config, payList} from './config'
  import {Encrypt} from '../util/crypto';
  import Clipboard from 'clipboard';

  const prefixCls = "pay";

  var validateQQ = (rule, value, callback) => {
    if (value.length < 5) {
      callback(new Error('QQ'));
    } else {
      callback();
    }
  };
  export default {
    data() {
      return {
        config: config,
        loading: false,
        img: '',
        payImg: '/images/loading.gif',
        box1: false,
