        fpbox: false,
        datetime: '',
        datecheck: null,
        ordercheck: null,
        fpform: {},
        fpoption: {
          emptyBtn: false,
          column: [{
            label: '',
            prop: 'orderid',
            disabled: true,
            span: 24
          }, {
            label: '',
            prop: 'mc',
            rules: [{
              required: true,
              message: "",
              trigger: "blur"
            }],
            span: 24
          }, {
            label: '',
            prop: 'sh',
            rules: [{
