            }],
            span: 24
          }
            /*, {
              label: '',
              prop: 'sjr',
              span: 24
            }, {
              label: '',
              span: 24,
              prop: 'lxfs',
            }, {
              label: '',
              prop: 'sjdz',
              type: 'textarea',
              span: 24
            }*/
            , {
              label: '',
              prop: 'bz',
              span: 24
            }]
        },
        box: false,
        cBox: false,
        list: config,
        id: '',
        form: {
          orderid: '',
          datetime: '',
          day: 0,
          type: 1,
          qq: '',
          flag: false,
        },
        option: {
          menuPostion: 'left',
          emptyBtn: false,
          submitBtn: false,
          column: [{
            label: '',
            labelWidth: 1,
            formslot: true,
            row: true,
            prop: 'default'
          }, {
            label: '',
            formslot: true,
            row: true,
            prop: 'name'
          }, {
            label: '',
            type: 'radio',
            border: true,
            formslot: true,
            dicData: config,
            span: 24,
            row: true,
            prop: 'version'
          }, {
            label: '',
            type: 'radio',
            border: true,
            display: false,
            dicData: [{
              label: '',
              value: 0
            }],
            row: true,
            prop: 'day'
          }, {
            label: '',
            formslot: true,
            row: true,
            prop: 'defaults'
          }, {
            label: '',
            formslot: true,
            labelWidth: 1,
            row: true,
            prop: 'money'
          }, {
            label: '',
            type: 'radio',
            border: true,
            dicData: payList,
            formslot: true,
            row: true,
            prop: 'type'
          }, {
            label: 'QQ',
            span: 7,
            row: true,
            rules: [{
              required: true,
              message: "QQ",
              trigger: "blur"
            },
              {
                validator: validateQQ,
                trigger: 'blur'
              }],
            prop: 'qq'
          }, {
            label: '',
            formslot: true,
            row: true,
            prop: 'introduction'
          }]
        }
      }
    },
    watch: {
      $route() {
        this.init();
      },
      'box'(val) {
        if (!val) {
          clearInterval(this.ordercheck);
          clearInterval(this.datecheck);
        }
      },
      'form.version': {
        handler(val) {
          if (this.form.version === 0 || this.form.version === 2) {
            this.form.type = 1;
          }
          const obj = config.filter(ele => ele.value === val)[0];
          this.form.name = obj.label;
          this.form.money = obj.money;
          this.$router.push({
            path: '/pay',
            query: {
              version: val
            }
          })
        }
      }
    },
    created() {
      this.init();
    },
    mounted() {

    },
    computed: {
      datetimeList() {
        return this.datetime.split('')
      },
      className() {
        return this.form.type === 2 ? `${prefixCls}--bule` : `${prefixCls}--green`
      },
      btncolor() {
        return this.form.type === 2 ? 'primary' : 'success'
      },
      imgsrc() {
        return this.form.type === 2 ? '/images/zgyhicon.png' : ' /images/wxicon.png'
      },
      text() {
        return this.form.type === 2 ? '' : ''
      }
    },
    methods: {
      copy(className) {
        let clipboard = new Clipboard('.' + className);
        clipboard.on('success', e => {
          window.console.log(e);
          this.$message.success('');
          // 
          clipboard.destroy();
        });
        clipboard.on('error', e => {
          // 
          window.console.log(e);
          this.$message.success('');
          // 
          clipboard.destroy()
        })
      },
      isCompany() {
        return this.form.type === 2;
      },
      handleEmpty(){
        this.fpform.mc = '';
        this.fpform.sh = '';
        this.fpform.bz = '';
      },
      handleSelect(item) {
        if (item.href) {
          window.location.href = item.href;
          return
        }
        this.form.version = item.value;
      },
      init() {
        this.form.version = Number(this.$route.query.version)
        if (!this.form.version) this.form.version = 0
      },
      insert(data, table) {
        const query = this.Bmob.Query(table || 'order');
        Object.keys(data).forEach(ele => {
          if (ele.includes('$')) {
            query.set(ele.replace('$', '') + '_text', data[ele])
          } else {
            query.set(ele, data[ele])
          }
        });
        return query.save()
      },
      update(id, data, table) {
        const form = this.deepClone(data);
        const query = this.Bmob.Query(table || 'order');
        query.set('id', id)
        delete form.createdAt;
        delete form.updatedAt;
        delete form.objectId;
        delete form.key;
        delete form.introduction;
        Object.keys(form).forEach(ele => {
          if (form[ele]) {
            query.set(ele, form[ele]) //objectId
          }

        })
        return query.save()
      },
      dingPush() {
        this.$httpajax.post('https://avuejs.com/sendmsg', {
          qq: this.form.qq,
          type: this.form.$type,
          version: this.form.$version,
          money: this.form.money,
          tenant: 1,
          orderid: this.form.orderid
        })
      },
      tradeNo() {
        const now = new Date()
        const year = now.getFullYear();
        let month = now.getMonth() + 1;
        let day = now.getDate();
        let hour = now.getHours();
        let minutes = now.getMinutes();
        let seconds = now.getSeconds();
        String(month).length < 2 ? (month = "0" + month) : month;
        String(day).length < 2 ? (day = "0" + day) : day;
        String(hour).length < 2 ? (hour = "0" + hour) : hour;
        String(minutes).length < 2 ? (minutes = "0" + minutes) : minutes;
        String(seconds).length < 2 ? (seconds = "0" + seconds) : seconds;
        const yyyyMMddHHmmss = `${year}${month}${day}${hour}${minutes}${seconds}`;
        this.id = Math.random().toString(36).substr(2, 6);
        return yyyyMMddHHmmss + '_' + this.id
      },
      handleBox() {
        if (this.form.version === 0) {
          this.box1 = true;
        } else {
          this.box2 = true;
        }
      },
      handleFpSubmit(form, done) {
        this.fpform.flag = false;
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.insert(this.fpform, 'ticket').then(() => {
            this.$httpajax.post('https://avuejs.com/sendfp',
              Object.assign(this.fpform, this.form,
                {
                  tenant: 1,
                  sjr: '-',
                  lxfs: '-',
                  sjdz: '-'
                }));
            this.fpbox = false;
            this.$message.success('');
            this.msgtip();
            done();
          })
        }).catch(() => {
          done();
        })
      },
      queryOrderid() {
        clearInterval(this.ordercheck);
        this.ordercheck = setInterval(() => {
          const query = this.Bmob.Query('order')
          query.equalTo("orderid", "==", this.form.orderid);
          query.find().then(res => {
            const flag = res[0].flag;
            if (flag) {
              this.box = false;
              if ([1, 2, 3].includes(this.form.version)) {
                this.fpbox = true;
              } else {
                this.msgtip();
              }
              if (this.box === false) {
                clearInterval(this.ordercheck);
                clearInterval(this.datecheck);
              }
            }
          });
        }, 1000);
      },
      msgtip() {
        this.$alert(`${this.form.qq}@qq.com`, '', {
          confirmButtonText: '',
          callback: () => {
            this.$router.push('/doc/notice')
          }
        });
      },
      queryTime() {
        this.datetime = "05:00";
        var maxtime = 5 * 60 - 1; //
        const CountDown = () => {
          if (maxtime >= 0) {
            this.update(this.form.objectId, {
              datetime: new Date((new Date().getTime() - 1000 * 20)).Format('yyyy-MM-dd hh:mm:ss')
            })
            var minutes = Math.floor(maxtime / 60);
            var seconds = Math.floor(maxtime % 60);
            if (minutes < 10) minutes = '0' + minutes
            if (seconds < 10) seconds = '0' + seconds
            this.datetime = minutes + ":" + seconds
            --maxtime;
          } else {
            this.box = false;
            clearInterval(this.datecheck);
          }
        };
        clearInterval(this.datecheck);
        this.datecheck = setInterval(() => CountDown(), 1000);
      },
      loadPayImg() {
        this.payImg = "/images/loading.gif";
        const datetime = new Date().Format('yyyyMMddhhmmss');
        this.$httpajax.post(this.website.PAY.url, Object.assign(this.form, {key: Encrypt(datetime)})).then(resp => {
          if (resp.data.code === "200") {
            this.payImg = resp.data.data;
          } else {
            this.$message.error("");
          }
        });

      },
      handleSubmit(form, done) {
        done();
        this.form.qq = this.form.qq.trim();
        let numReg = /^[0-9]+$/;
        let numRe = new RegExp(numReg);
        if (!numRe.test(this.form.qq)) {
          this.$message.error('qq');
          return false;
        }
        this.loading = true;
        const query = this.Bmob.Query("order");
        query.equalTo("qq", "==", this.form.qq);
        query.equalTo("type", "==", this.form.type);
        query.equalTo("version", "==", this.form.version);
        query.equalTo("flag", "==", false);
        query.find().then(res => {
          if (this.validatenull(res[0])) {
            this.form.orderid = this.tradeNo();
            this.fpform.orderid = this.form.orderid;
            this.form.datetime = (new Date()).Format('yyyy-MM-dd hh:mm:ss');
            if (this.form.qq.indexOf("@qq.com") > 0) {
              this.form.qq = this.form.qq.replace("@qq.com", "");
            }
            let data = this.deepClone(this.form)
            delete data.objectId;
            delete data.createdAt;
            delete data.updatedAt;
            delete data.key;
            delete data.introduction;
            // bladex
            if (data.version === 3) {
              data.money = 3500;
            }
            this.insert(data).then((res) => {
              this.form.objectId = res.objectId;
              this.dingPush();
              if (this.isCompany()) {
                this.cBox = true;
              } else {
                this.box = true;
                this.loadPayImg();
                this.queryOrderid();
                this.queryTime();
              }
              done();
              this.loading = false;
            });
          } else {
            let data = res[0];
            // 
            if (data.version === 3) {
              data.money = config[1].money;
            }
            this.form = data;
            // 
            this.update(this.form.objectId, {
              datetime: (new Date()).Format('yyyy-MM-dd hh:mm:ss')
            });
            this.fpform.orderid = this.form.orderid;
            this.id = this.form.orderid.split("_")[1];
            if (this.isCompany()) {
              this.cBox = true;
            } else {
              this.box = true;
              this.loadPayImg();
              this.queryOrderid();
              this.queryTime();
            }
            done();
            this.loading = false;
          }
        });


      }
    }
  }
</script>

